body {
    margin: 0px;
    background-color: orange;
}

.root {
    margin: 0;
    background-color: green;
    padding: 0;
}





.aboutme-wrapper {
    margin: 0;
    padding: 0;
}
.button{
    position: absolute;
    top: 0%;
    right: 0%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 25vw 25vw 0;
    border-color: transparent rgb(190, 188, 204) transparent transparent;
}
.button2{
    position: absolute;
    bottom: 0%;
    left: 0%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 25vw 0 0 25vw;
    border-color: transparent transparent transparent rgb(190, 188, 204);
}

.button-name {
    letter-spacing: 2px;
    position: absolute;
    top: 0%;
    right: 3%;
}
.aboutme-header {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    background-color:  white;
}

.aboutme-header h2 {
    margin: 0;
    letter-spacing: 5px;
    padding: 60px 40px 40px 40px;
    font-family: 'Lato', sans-serif;
    color: rgb(65, 64, 64);
}

.image-deco{
    position: relative;
    height: 350px;
}
.deco{
    position: absolute;
    left: 50%;
    top: 35%;
    width: 180px;
    height: 180px;
    background-color: rgb(254, 249, 210, 0.1);
    transform-origin: left;
    transform: translate(-35%, -75%)  rotate(45deg);
    border: 12px double #e5b3a3;
    opacity: .7;
    border-radius: 5px;
}

.profile-photo {
    position: absolute;
    left: 50%;
    top: 40%;
    width: 180px;
    height: 180px;
    transform: translate(-50%, -50%);
    border: 12px double	#78a085;
    opacity: .9;
    border-radius: 5px;
}
.aboutme-body{
    background-color: #e5b3a3;
    margin: 0;
    padding: 50px 30px;
}

.aboutme-body .profile-1 p{
    font-size: 1.2em;
    font-family: 'Roboto', sans-serif;
    letter-spacing: 1px; 
}

.aboutme-body .profile-1 h5{
    letter-spacing: 4px;
    font-size: 1.2em;
    font-family: 'Lato', sans-serif;
}
.container {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    /* background-color: rgba(1, 1, 1, 1); */
    position: relative;;
    /* border-bottom: 5px solid #5079af; */
}
.container p {
    color: rgb(65, 64, 64);
}

.japa {
    position: absolute;
    top: 0%;
    right: 4%;
    width: 100px;
    text-align: right;
    font-size: 1.2rem;
    font-family: "M PLUS Rounded 1c";
    letter-spacing: 2px;
    /* background-color: indianred; */
}

.title{
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.title p{
    font: helvetica;
    color: white;
    font-size: 2.5em;
    text-align: center;
    margin: 0;
    padding-bottom: 20px;
    /* width: 60vw; */
    /* font-weight: 300; */
    letter-spacing: 2px;
    font-family: 'Lato', sans-serif;
    border-bottom: 2px solid white;
}

.title h6{
    color: white;
    font-size: 1.3em;
    text-align: center;
    margin-top: 10px;
    padding: 0;
    font-family: 'Roboto', sans-serif;
}
.navwrapper{
    position: absolute;
    bottom: 10%;
    left: 0%;
    /* transform: translate(-50%, -50%); */
    z-index: 4;
    /* background-color: red; */
}

.navmenu{
    position: relative;
    width: 180px;
    height: 120px;
    /* border: 1px solid green */
}


.navmenu .navcontroller{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* z-index: 4; */
    width: 80px;
    height: 80px;
    background-color: #da7c7f;
    font-size: 20px;
    text-align: center;
    line-height: 80px;
    border-radius: 50%;
    cursor: pointer;
    z-index: 3;
}

.navmenu .navclosebtn{
    display: none;
    color: white;
    font-family: 'Roboto', sans-serif;
}

.navmenu .navopenbtn{
    color: white;
    font-family: 'Roboto', sans-serif;
}

.navmenu.active .navopenbtn{
    display: none;
}

.navmenu.active .navclosebtn{
    display: inline;
}


.navmenu .navbtn{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* z-index: 4; */
    width: 75px;
    height: 75px;
    background-color: lightpink;
    font-size: 15px;
    text-align: center;
    line-height: 75px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 600ms ease-in-out 0ms;
}

.navmenu.active div:nth-child(2){
    top: -25%;
    left: 50%;
    transform: translate(-50%, -75%) rotateY(360deg);
}

.navmenu.active div:nth-child(3){
    top: 7%;
    left: 100%;
    transform: translate(-55%, -60%) rotateY(360deg);
}

.navmenu.active div:nth-child(4){
    top: 100%;
    left: 100%;
    transform: translate(-50%, -75%) rotateY(360deg);
}



@media screen and (min-width: 1224px) {

img{
    /* padding-top: 50px; */
    object-position: right top;
    width: 100vw;
    object-fit: cover;
    height: 100%;
    /* filter: blur(1px); */
}


}
@media screen and (max-width: 1223px) {

img {
    width: 100vw;
    object-fit: cover;
    height: 100%;
    object-position: 75% top;
}

}

.projectwrapper{
    padding: 50px 30px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
}
.project-header h2 {
    color: rgb(65, 64, 64);
    border-bottom: 5px solid #b4a2ae;
    margin: 0;
    letter-spacing: 5px;
    padding: 20px 40px;
    font-size: 1.5em;
    font-family: 'Lato', sans-serif;
}

.project-body{
    margin: 40px 0 0 0 ;
    /* background/-color: red; */
    z-index: 5;
}
.project1{
    margin: 0;
    /* background-color: rgb(230, 207, 207); */
    padding:0;
    font-size: 3.5vw;
    font-family: sans-serif;
    letter-spacing: 1.2px; 
}
.project1 h2{
    /* font-size: 1.2em; */
    font-family: 'Roboto', sans-serif;
    margin: 0;
}
.project1 h2{
    /* font-size: 1.2em; */
    font-family: 'Roboto', sans-serif;
    margin: 0;
}
.stackwrapper{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}
.teckstack{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.teckstack span{
    font-family: 'Roboto', sans-serif; 
    letter-spacing: 1.2px; 
    font-size: 0.8rem;
    padding: 0 4px;
}
.project1 h3{
    /* font-size: 1.2em; */
    /* font-family: 'Roboto', sans-serif; */
    margin: 0;
}
.projectimage{
    
    width: 100%;
    margin: 30px 0;
    position: relative;
}
.link-wrapper{
    position: absolute;
    /* margin-top: 20px; */
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
    transition: 1.4s;
}
.project-link{
    transition: 1s;
    padding:15px;
    border: 1px gray solid ;
    background-color: red;
    opacity: 0;
    border-radius: 10px;
    border: 3px double white;
    background-color: rgb(236, 179, 162);
    text-decoration: none;
    color: rgb(44, 43, 43)
}
.link-wrapper:hover {
    background-color: rgba(107, 110, 110, 0.9);
}
.link-wrapper:hover  .project-link{
    opacity: 1;
}

.project-link span {
    font-family: 'Lato', sans-serif; 
    letter-spacing: 1.2px; 
    font-size: 0.8rem;
} 
.project-link:hover {
    border: 5px groove white;
    background-color: rgb(173, 209, 209);
    transition: 0.2s;
}
.projectimage img{
    width: 100%;
    z-index: 4;
}

.border{
    border-top: 5px double rgb(69, 107, 107);
    border-bottom: 5px double darkslategray;
}

